<template>
    <div id="app">
        <Head class="header" activeUrl="register" ></Head>
        <div class="main-body content">
            <css-doodle>
                --color: @p(#51eaea, #fffde1, #ff9d76, #FB3569);
                :doodle {
                @grid: 30x1 / 18vmin;
                --deg: @p(-180deg, 180deg);
                }
                :container {
                perspective: 30vmin;
                }
                :after, :before {
                content: '';
                background: var(--color);
                @place-cell: @r(100%) @r(100%);
                @size: @r(6px);
                @shape: heart;
                }

                @place-cell: center;
                @size: 100%;

                box-shadow: @m2(0 0 50px var(--color));
                background: @m100(
                radial-gradient(var(--color) 50%, transparent 0)
                @r(-20%, 120%) @r(-20%, 100%) / 1px 1px
                no-repeat
                );

                will-change: transform, opacity;
                animation: scale-up 12s linear infinite;
                animation-delay: calc(-12s / @size() * @i());

                @keyframes scale-up {
                0%, 95.01%, 100% {
                transform: translateZ(0) rotate(0);
                opacity: 0;
                }
                10% {
                opacity: 1;
                }
                95% {
                transform:
                translateZ(35vmin) rotateZ(@var(--deg));
                }
                }
            </css-doodle>
        </div>
        <Footer></Footer>
    </div>
</template>

<script>

    export default {
        name: 'app',
        data: function () {
            return {

            }
        },
        methods: {
            handleScroll(){
                let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
                let oneSize = 801/1980;

                if(scrollTop >= document.body.clientWidth * oneSize - 100){
                    this.type = 1
                } else {
                    this.type = 2
                }
            },
        },
        mounted() {
            window.addEventListener('scroll',this.handleScroll)
        },
        beforeDestroy: function () {

        },
        components: {}
    }
</script>

